import React from 'react';
import PropTypes from 'prop-types';
import { DEFAULT_PORTAL_DOM_NODE_ID } from './constants';
import { warning } from '../functions';

let warnedParentDOMNodeAndParentDOMNodeIdSpecified = false;

export const resetWarnings = () => {
  warnedParentDOMNodeAndParentDOMNodeIdSpecified = false;
};

const defaultValue = {
  parentDOMNode: undefined,
  parentDOMNodeId: DEFAULT_PORTAL_DOM_NODE_ID,
};

const PortalContext = React.createContext(defaultValue);

/**
 * @typedef {Object} PortalContextProviderProps
 * @prop {React.ReactNode} children
 * @prop {HTMLElement} [parentDOMNode]
 * @prop {string} [parentDOMNodeId]
 */

/**
 * @name PortalContext.Provider
 * @type {React.Component<PortalContextProviderProps>}
 */
class PortalContextProvider extends React.Component {
  static propTypes = {
    children: PropTypes.node.isRequired,
    /* eslint-disable react/no-unused-prop-types */
    /**
     * The DOM node that should be used to house portals.
     *
     * ⚠️ **NOTE:** One of `parentDOMNode` or `parentDOMNodeId` can be
     * specified, but not both. If `parentDOMNode` is specified,
     * `parentDOMNodeId` will be ignored.
     */
    parentDOMNode: PropTypes.instanceOf(HTMLElement),
    /**
     * The _id_ of the DOM node that should be used to house portals. If a node
     * with this id does not already exist in the application, it will be
     * created by the `Portal` component under the `document.body` element.
     *
     * ⚠️ **NOTE:** One of `parentDOMNode` or `parentDOMNodeId` can be
     * specified, but not both. If `parentDOMNode` is specified,
     * `parentDOMNodeId` will be ignored.
     */
    parentDOMNodeId: PropTypes.string,
    /* eslint-enable react/no-unused-prop-types */
  };

  static defaultProps = {
    parentDOMNode: undefined,
    parentDOMNodeId: DEFAULT_PORTAL_DOM_NODE_ID,
  };

  static getDerivedStateFromProps(
    { parentDOMNode, parentDOMNodeId },
    prevState,
  ) {
    const nextState = {};

    if (parentDOMNode !== prevState.parentDOMNode) {
      nextState.parentDOMNode = parentDOMNode;

      /* istanbul ignore else */
      if (parentDOMNode) {
        // Unset `parentDOMNodeId` so that it can't be in conflict with
        // `parentDOMNode`.
        nextState.parentDOMNodeId = undefined;
      }
    }

    if (!parentDOMNode && parentDOMNodeId !== prevState.parentDOMNodeId) {
      nextState.parentDOMNodeId = parentDOMNodeId;
    }

    // Print warnings if both `parentDOMNode` and `parentDOMNodeId` are defined.
    //
    // NOTE: Don't print the warning if `parentDOMNodeId` is its default value
    // - it's likely that someone just forgot to override the default with
    // `undefined`.
    if (
      !warnedParentDOMNodeAndParentDOMNodeIdSpecified &&
      parentDOMNode &&
      parentDOMNodeId &&
      parentDOMNodeId !== DEFAULT_PORTAL_DOM_NODE_ID
    ) {
      warning(
        false,
        'Both `parentDOMNode` and `parentDOMNodeId` have been specified on a ' +
          '`PortalContext.Provider` component. Only one of these should be ' +
          'supplied. `parentDOMNodeId` will be ignored.',
      );
      warnedParentDOMNodeAndParentDOMNodeIdSpecified = true;
    }

    return Object.keys(nextState).length > 0
      ? nextState
      : /* istanbul ignore next */ null;
  }

  /* eslint-disable react/no-unused-state */
  state = {
    parentDOMNode: undefined,
    parentDOMNodeId: undefined,
  };
  /* eslint-enable react/no-unused-state */

  render() {
    const { children } = this.props;

    return (
      <PortalContext.Provider value={this.state}>
        {children}
      </PortalContext.Provider>
    );
  }
}

export default {
  Consumer: PortalContext.Consumer,
  Provider: PortalContextProvider,
};
